.App {
  text-align: left;
  color: black;
}

/* header */

#Dheader {
  margin-top: 8%;
  margin-bottom: 20px;
  font-size: 1.5rem;
  line-height: 2rem;
  font-size: 25px;
  font-weight: 450;
}
#email {
  color: grey;
}

/* Description*/

.Ddesc {
  padding-left: 17.5%;
  margin: 18px;
}

.Ddesc p {
  margin-bottom: 22px;
}
.Ddesc a {
  color: rgb(29 78 216);
}

/* ICONS*/
#dico {
  display: flex;
  align-items: center;
  margin-top: 40px;
}

.iico {
  width: 28px;
  height: 28px;
  margin-right: 15px;
}
.iicoIM {
  width: 23.5px; /* Reduced icon size for mobile */
  height: 23.5px; /* Reduced icon size for mobile */
  margin-right: 15px; /* Reduced space between icons */
}
    
.dlink {
  margin-right: 15px;
}

/* projects */

#dproj {
  margin-top: 4.5%;
  font-size: 20px;
  font-weight: 400;
}
.dProjSpan {
  border: 1px solid black;
  padding: 10px;
  margin: 10px;
  text-align: left;
  width: 80%;
  color: black;
}
.dProjSpanHead {
  font-size: 1.2rem;
}
.dProjSpanHead a {
  color: black;
}

.DlistInfo {
  margin-right: 8px;
}

.DlistInfo a {
  color: red;
  text-decoration: underline;
  cursor: pointer;
}

ul {
  list-style: none;
  padding-left: 0;
}

li::before {
  content: "-";
  margin-right: 5px;
}

.DlistDiv {
  display: flex;
  padding: 5px;
  align-items: center;
  justify-content: flex-start;
}
#DlistDivDown ::before {
  content: none !important;
}

.DlistDesc a {
  text-decoration: underline;
  color: rgb(29 78 216);
}

/* Mobile Styles */
@media (max-width: 768px) {
  #Dheader {
    margin-top: 22%; /* Adjusted margin for mobile */
    font-size: 20px; /* Adjusted font size for mobile */
  }

  #dico {
    margin-top: 20px; /* Adjust margin for social icons */
    padding-left: 5%; /* Reduced left padding for mobile */
    padding-right: 5%; /* Added right padding for mobile */
  }

  .Ddesc {
    padding-left: 5%; /* Reduced left padding for mobile */
    margin: 10px; /* Adjusted margin for mobile */
  }

  .Ddesc p {
    margin-bottom: 15px; /* Reduced spacing between paragraphs */
  }

  .iico {
    width: 24px; /* Reduced icon size for mobile */
    height: 24px; /* Reduced icon size for mobile */
    margin-right: 10px; /* Reduced space between icons */
  }
  .iicoIM {
    width: 20px; /* Reduced icon size for mobile */
    height: 20px; /* Reduced icon size for mobile */
    margin-right: 10px; /* Reduced space between icons */
  }

  #dproj {
    font-size: 18px; /* Adjusted font size for projects section */
  }

  .dProjSpan {
    width: 90%; /* Make project box wider on mobile */
  }
